@import "../../includes/includes";


.hero {
  &__content {
    width: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    color: $white;
    justify-content: flex-end;
  }
  &__body {
    width: 50%;
    min-height: 450px;
    justify-content: center;

    .title {
      @include font-sizer(48, 60);
    }
    .copy {
      @include font-sizer(18, 24);
    }
  }
}


